import React, { useState, useContext } from "react";
import { useHistory, Link } from "react-router-dom";
import { useAlert } from "react-alert";
import UserContext from "../utils/userContext";
import { Container, FormGroup, Form, Label, Input, Row, Col } from "reactstrap";
import API from "../utils/API";

function NewUserType() {
  // const { authenticationState } = useContext(UserContext);
  const [formObject, setFormObject] = useState("");
  // const history = useHistory();
  // const alert = useAlert();

  function handleFormSubmit(event) {
    event.preventDefault();
  }

  function handleInputChange(event) {
    console.log(event.target);
    const { name, value } = event.target;
    setFormObject({ [name]: value });
    console.log(value);
    // if bazaarID == {join code length} => check bazaar code API to validate and then activate the 'join' button
    if (value.length === 9) {
      API.getBazaar(formObject.bazaarCode).then((res) => {
        console.log(res);
        // const bazaarId = res.data._id;
        // console.log(bazaarId);
      });
    }
  }

  return (
    <Container>
      <Row className="mt-5 px-5">
        <Col>
          <Link
            to="/newusercreds/gm"
            alt="Create a new Bazaar"
            className="text-center"
          >
            <button className="splashBtn">Create a new Bazaar</button>
          </Link>
        </Col>
      </Row>
      <Row className="mt-5 px-5">
        <Col>
          <Link to="/newnewusercreds/player?bazaarid=">
            <button>Join a bazaar</button>
          </Link>
          <Form onSubmit={handleFormSubmit} className="text-center">
            <FormGroup>
              <Label for="bazaarCode">Bazaar Join Code</Label>
              <Input
                type="text"
                name="bazaarCode"
                id="bazaarCode"
                placeholder="Bazaar Code Here"
                onChange={handleInputChange}
              />
            </FormGroup>
          </Form>
        </Col>
      </Row>
    </Container>
  );
}

export default NewUserType;
